'use client';

const DiagnosisLeft = ({ documentData, issues, userInput, onUserInputChange }) => {
  return (
    <div className="diagnosis-left bg-white rounded-2xl shadow-sm p-6 h-[calc(100vh-140px)] overflow-y-auto">
      <div className="diagnosis-header flex justify-between items-center pb-4 border-b border-gray-100 mb-5">
        <div className="diagnosis-title text-xl font-bold text-gray-800 flex items-center">
          <svg width="24" height="24" viewBox="0 0 24 24" className="mr-3">
            <path d="M20.24 12.24a6 6 0 00-8.49-8.49L5 10.5V19h8.5l6.74-6.76z" stroke="#5d8a9e" strokeWidth="2" fill="none"/>
            <line x1="16" y1="8" x2="2" y2="22" stroke="#5d8a9e" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
          診斷結果
        </div>
        <div className="doc-info flex items-center text-gray-600 text-sm">
          <div className="doc-type bg-blue-600 text-white px-3 py-1 rounded-full text-xs mr-3">
            {documentData.documentType}
          </div>
          <div className="school-info font-semibold">
            {documentData.school}
          </div>
        </div>
      </div>
      
      <div className="content-panel mb-5">
        <ul className="diagnosis-list space-y-4">
          {issues.map((issue, index) => (
            <li key={index} className={`diagnosis-item ${issue.type} p-4 rounded-lg bg-gray-50 border-l-4`}>
              <div className="issue-code font-bold text-gray-800 inline-block mr-1">{issue.code}</div>
              <span className="issue-title font-bold text-gray-800">{issue.title}</span>
              <div className="issue-desc mt-2 text-gray-600 leading-relaxed">{issue.desc}</div>
            </li>
          ))}
        </ul>
      </div>
      
      <div className="user-input-section mt-8">
        <div className="user-input-title text-lg font-bold text-gray-800 mb-4 flex items-center">
          <svg width="20" height="20" viewBox="0 0 24 24" className="mr-2">
            <circle cx="12" cy="12" r="10" stroke="#5d8a9e" strokeWidth="2" fill="none"/>
            <path d="M9.09 9a3 3 0 015.83 1c0 2-3 3-3 3" stroke="#5d8a9e" strokeWidth="2" strokeLinecap="round"/>
            <circle cx="12" cy="17" r="0.5" stroke="#5d8a9e" strokeWidth="2" fill="#5d8a9e"/>
          </svg>
          自我疑問與回饋
        </div>
        <div className="user-input-container bg-gray-50 rounded-lg p-4">
          <textarea 
            className="user-textarea w-full min-h-[120px] p-3 border border-gray-200 rounded-lg resize-y text-gray-700"
            placeholder="請輸入您對文件的疑問、已知問題或需要特別關注的部分..."
            value={userInput}
            onChange={onUserInputChange}
          ></textarea>
          <div className="hint-text text-gray-400 text-xs mt-2 italic">若無疑問可留空此欄位</div>
        </div>
      </div>
    </div>
  );
};

export default DiagnosisLeft; 