'use client';

const DiagnosisRight = ({ documentContent, onContentChange }) => {
  const handleContentChange = (e) => {
    onContentChange(e.target.value);
  };

  return (
    <div className="diagnosis-right bg-white rounded-2xl shadow-sm p-6 h-[calc(100vh-140px)] overflow-y-auto">
      <div className="diagnosis-header flex justify-between items-center pb-4 border-b border-gray-100 mb-5">
        <div className="diagnosis-title text-xl font-bold text-gray-800 flex items-center">
          <svg width="24" height="24" viewBox="0 0 24 24" className="mr-3">
            <path d="M14 3v4a1 1 0 001 1h4" stroke="#5d8a9e" strokeWidth="2" fill="none"/>
            <path d="M17 21H7a2 2 0 01-2-2V5a2 2 0 012-2h7l5 5v11a2 2 0 01-2 2z" stroke="#5d8a9e" strokeWidth="2" fill="none"/>
            <path d="M9 9h6M9 13h6M9 17h4" stroke="#5d8a9e" strokeWidth="2" strokeLinecap="round"/>
          </svg>
          文件內容（可編輯）
        </div>
      </div>
      
      <textarea 
        className="editable-content w-full h-[calc(100%-60px)] p-4 bg-gray-50 border border-gray-200 rounded-lg text-gray-700 leading-relaxed resize-none" 
        value={documentContent}
        onChange={handleContentChange}
      ></textarea>
    </div>
  );
};

export default DiagnosisRight; 